'use strict';

/**
* Gamma distribution logarithm of cumulative distribution function (CDF).
*
* @module @stdlib/stats-base-dists-gamma-logcdf
*
* @example
* import logcdf from '@stdlib/stats-base-dists-gamma-logcdf' ;
*
* var y = logcdf( 2.0, 0.5, 1.0 );
* // returns ~-0.047
*
* @example
* import { factory as factory } from '@stdlib/stats-base-dists-gamma-logcdf' ;
*
* var logcdf = factory( 3.0, 1.5 );
* var y = logcdf( 1.0 );
* // returns ~-1.655
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import main from './main.js' ;
import factory from './factory.js' ;


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;
